import React, { Component } from "react";

export class TaskTimer extends Component {
  constructor(props) {
    super(props);

    this.state = {
      hours: 0,
      min: 0,
      sec: 0
    };
  }

  componentDidMount() {
    // update every second
    this.interval = setInterval(() => {
      const date = this.calculateCountdown(this.props.date);
      date ? this.setState(date) : this.stop();
    }, 1000);
  }

  componentWillUnmount() {
    this.stop();
  }

  calculateCountdown(endDate) {
    let diff = (Date.parse(new Date(endDate)) - Date.parse(new Date())) / 1000;

    // clear countdown
    if (diff <= 0) return false;

    const timeLeft = {
      hours: 0,
      min: 0,
      sec: 0,
      millisec: 0
    };

    // calculate time difference between now and end time

    if (diff >= 3600) {
      // 60 * 60
      timeLeft.hours = Math.floor(diff / 3600);
      diff -= timeLeft.hours * 3600;
    }
    if (diff >= 60) {
      timeLeft.min = Math.floor(diff / 60);
      diff -= timeLeft.min * 60;
    }
    timeLeft.sec = diff;

    return timeLeft;
  }

  stop() {
    clearInterval(this.interval);
  }

  addLeadingZeros(value) {
    value = String(value);
    while (value.length < 2) {
      value = "0" + value;
    }
    return value;
  }

  render() {
    const countDown = this.state;

    return (
      <h6>
        <span>{this.addLeadingZeros(countDown.hours)}:</span>
        <span>{this.addLeadingZeros(countDown.min)}:</span>
        <span>{this.addLeadingZeros(countDown.sec)}</span>
      </h6>
    );
  }
}

TaskTimer.defaultProps = {
  date: new Date()
};

export default TaskTimer;
